body {
    overflow-x: hidden;
}

.page-main {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
}

.background-city {
    width: 100%;
    height: 732px;
    background-image: url(../img/Group_mobile.svg);
    background-repeat: no-repeat;
    margin-top: -500px;
    margin-bottom: -250px;

    animation: cityAnim 1s;
    @keyframes cityAnim {
        from {
            transform: translate(0px, 1000px);
            opacity: 0;
        }
        to {
            transform: translate(0px, 0px);
            opacity: 1;
        }
    }
    // &::before {
    //     position: absolute;
    //     z-index: -3;
